import { init, addMessages, t, getLocaleFromNavigator } from 'precompile-intl-runtime';
import en from '../locales/en';
import es from '../locales/es';

function setupI18n() {
    
    addMessages("en", en);
    addMessages("es", es);

    init({
        fallbackLocale: 'en',
        initialLocale: getLocaleFromNavigator()
    });
}


export { t, setupI18n };