<script>
	export let type,
        name = undefined,
        value = "",
        id = undefined,
        style = undefined,
        title = undefined,
        placeholder = "",
        required = undefined,
        maxlength = undefined,
        minlength = undefined,
        autocomplete = undefined,
        pattern = undefined,
        disabled = undefined,
        ariaInvalid = undefined;

	let internalPlaceholder = placeholder,
        classes = '';
	export { classes as class };

    function clearTest(e) {
        if (e.target.className.indexOf('no-outline') != -1) {
            if (!e.target.value && e.target.placeholder != "") {
                e.target.placeholder = "";
            } else if (!e.target.value) {
                e.target.placeholder = internalPlaceholder;
            }
        }
    }

    function handleInput(e) {
        value = type.match(/^(number|range)$/) ? +e.target.value : e.target.value;
    }
</script>

<input class="{classes}" {name} {type} {disabled} {id} {style} {value} {placeholder} {title} {required} {maxlength} {minlength} aria-invalid={ariaInvalid} {autocomplete} {pattern} on:click="{clearTest}" on:blur="{clearTest}" on:input={handleInput} data-testid="mainInput" />

