<script>
    import { getContext } from 'svelte';
    import { default as ContentAsset } from '../ContentAsset/ContentAsset.svelte';
    const { open } = getContext('simple-modal') ? getContext('simple-modal') : { open: undefined };
    export let popupContent = '',
        popupContentHTML = '',
        showPopup;

    if (popupContentHTML) {
        const contentComponent = function () {
            //Create a Fragment node to avoid duplicate content in DOM
            const fragmentedNode = document.createDocumentFragment();
            return new ContentAsset({
                target: fragmentedNode,
                props: {
                    contentHTML: popupContentHTML,
                },
            });
        };
        showPopup = () => open(contentComponent);
    } else {
        showPopup = () => open(popupContent);
    }
</script>

<div on:click={showPopup}>
    <slot />
</div>
